.b-button {
  background-color: #ffb72d;
  border: none;
  background: linear-gradient(to right, #68a2b8, #55bc96);
  border-radius: 20px;
  font-family: "montserratbold";
  color: #000000;
  padding: 15px 30px;
  transition: color 0.2s;
  font-size: 14px;
  display: inline-block; }
  .b-button:focus {
    outline: none; }
  .no-touchevents .b-button:hover,
  .touchevents .b-button:active {
    color: #fff; }
  .b-button--full {
    width: 100%; }
  .b-button--orange {
    background: #ffb72d; }
  .b-button--save {
    width: 100%;
    border-radius: 0;
    padding: 12px 30px; }
  .b-button--hello-page {
    width: 260px; }

@media (min-width: 1280px) {
  .b-button {
    font-size: 16px; } }



